@import "src/styles/constants";

.container {
  margin: 30px 0;
}

.titleAndButton {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.titleWrapper {
  display: flex;
  align-items: center;
  column-gap: 10px;

  & img {
    width: 18px;
    height: 18px;
    cursor: pointer;
  }
}

.title {
  font-weight: 600;
  font-size: 22px;
  line-height: 27px;
  color: $font-dark-color;
}




.search {
  margin-top: 30px;
}